
.btn-dark{
  background-color: #495259 !important;
}
.text-dark{
  color: #2C4456 !important;
}
.text-info{
  color: #BCD6EB !important;
}
.progress-bar{
  background: linear-gradient(to right,#FD8124, #FECA32);
  border-radius: 0rem 1rem 1rem 0rem;
  height: 25px;
}
.progress{
  background: transparent !important;
}
.reload{
  position: absolute;
  right: 1rem;
  top: 2rem;
  cursor: pointer;
}
.reload h1{
  font-size: 0.8rem;
}

.cuadre{
  width: 140px;
  height: 140px;
  border: 2px dashed #3333;
  border-radius: 0.5rem !important;
}
.cuadre.success{
  border: 2px solid #5AE0C9;
  box-shadow: 0 3rem 4rem #5ae0ca87 !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cuadre.danger{
  border: 2px solid #F4787D;
  box-shadow: 0 3rem 4rem #F4787D87 !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.correct{
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
}
.correct h1{
  margin: 0;
  font-size: 1.5rem;
  width: 100%;
  text-align: center;
  color: #B40614;
}
.correct span{
  font-size: 3rem;
  color: #16977E;
  margin: 0;
  text-align: center;
}
.cuadre h1{
  font-weight: 600;
  font-size: 5rem;
}
.display-6{
  font-size: 2.5rem;
}
.text-number{
  font-weight: 600;
  font-size: 5rem;
}
.numbers{
  background: #8A9FB1;
  width: auto;
  height: auto;
  position: absolute;
  left: -25px;
  right: -25px;
  bottom: -35px;
  box-shadow: 0 1rem 3rem rgba(0,0,0,.175) !important;
  border-radius: 0.5rem !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.position-relative{
  position: relative;
}
.number{
  cursor: pointer;
  width: 50px;
  height: 50px;
  background: #ffff;
  border-radius: 0.5rem !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.number:hover{
  background: #5AE0C9;
  color: #ffff;
}
.number h1{
  font-size: 1.5rem;
  margin: 0;
  font-weight: 600;
}
.text-des{
  color: #6A7680 !important;
  margin-bottom: 1.5rem;
}
.text-pro{
  font-size: 6rem;
  font-weight: bold;
}

